import idx from 'idx';

export default function search(query) {
  return window.__LUNR__.__loaded.then(() => {
    const {index, store} = idx(window, _ => _.__LUNR__.en);
    if (!query || !index || !store) return [];
    const results = index.search(query);
    return results.map(({ref}) => ({ref, ...store[ref]}));
  });
}
